import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { IComponentFormError } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { NgStyle } from '@angular/common';
import { consumerPollProducersForChange } from '@angular/core/primitives/signals';

@Component({
  selector: 'custom-input-form',
  standalone: true,
  imports: [NgStyle, ReactiveFormsModule, CustomAppErrorComponent],
  templateUrl: './custom-input-form.component.html',
  styleUrls: ['./custom-input-form.component.css'],
})
export class CustomInputFormComponent {
  @Input() class: string = '';
  @Input() labelClass: string = '';
  @Input() label: string = '';
  @Input() placeholder: string = '';
  @Input({ required: true }) name: string = '';
  @Input() type: 'text' | 'email' | 'password' | 'number' = 'text';
  @Input({ required: true }) controlName: string = '';
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input() pattern: string = '';
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
  @Input() height: string = '3.6em';
  @Input() disabled: boolean = false;

  isFocused = false;

  onFocus(): void {
    this.isFocused = true;
  }

  onBlur(): void {
    this.isFocused = false;
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }

  onValueChange() {
    if (
      !this.disabled &&
      this.parentForm.controls[this.controlName].value.length > 0
    ) {
      this.parentForm.controls[this.controlName].markAsTouched();
    }
  }

  ngOnInit() {
    // TODO: CHECK if should remove
    if (this.disabled) {
      this.parentForm.controls[this.controlName].disable();
    }
  }

  getMaxLength(): number | null {
    const control = this.parentForm.controls[this.controlName];

    if (!control || !control.validator) {
      return null;
    }

    // Trigger maxLength error with an infinitely long value
    const validatorResult = control.validator({
      value: { length: Infinity },
    } as any);

    // If maxLength validator exists, it will return an error with requiredLength
    if (validatorResult && validatorResult['maxlength']) {
      return validatorResult['maxlength'].requiredLength;
    }

    return null;
  }

  getMinLength(): number | null {
    const control = this.parentForm.controls[this.controlName];

    if (!control || !control.validator) {
      return null;
    }

    // Trigger minLength error with an infinitely long value
    const validatorResult = control.validator({
      value: { length: Infinity },
    } as any);

    // If minLength validator exists, it will return an error with requiredLength
    if (validatorResult && validatorResult['minlength']) {
      return validatorResult['minlength'].requiredLength;
    }

    return null;
  }
}
