import { Component, EventEmitter, input, Input, Output } from '@angular/core';
import {
  FormControl,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { IComponentFormError } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';

@Component({
  selector: 'custom-plate-input-form',
  imports: [ReactiveFormsModule, CustomAppErrorComponent],
  templateUrl: './custom-plate-number-input-form.component.html',
  styleUrl: './custom-plate-number-input-form.component.css',
})
export class CustomPlateNumberInputFormComponent {
  @Input() class: string = '';
  @Input() labelClass: string = '';
  @Input() label: string = '';
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) controlName: string = '';
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input() mainValidation: IComponentFormError[] = [];
  @Input() maxNumberLength: number = 4;
  @Input() maxLetterLength: number = 3;

  // maxLetterLength
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();

  PlateInputForm: FormGroup = new FormGroup({
    plateNumberNumeric: new FormControl('', [
      Validators.required,
      Validators.pattern('[0-9]{3,4}'),
    ]),
    plateNumberLetters: new FormControl('', [
      Validators.required,
      Validators.pattern('[A-Za-z]{3,4}'),
    ]),
  });

  ngOnInit() {
    // Initialize the form with values from the parent form

    if (this.parentForm.controls[this.controlName].value.length > 0) {
      const initialNumberValue =
        this.parentForm.controls[this.controlName].value.split(' ')[0] || '';
      const initialLettersValue =
        this.parentForm.controls[this.controlName].value.split(' ')[1] || '';

      this.PlateInputForm.patchValue({
        plateNumberNumeric: initialNumberValue,
        plateNumberLetters: initialLettersValue,
      });

      this.PlateInputForm.markAllAsTouched();
      this.PlateInputForm.updateValueAndValidity();
      this.parentForm.controls[this.controlName].updateValueAndValidity();
      this.parentForm.controls[this.controlName].markAllAsTouched();
    }
    // Subscribe to changes in the PlateInputForm to update the parent form
    this.PlateInputForm.valueChanges.subscribe(() => {
      const numberValue =
        this.PlateInputForm.get('plateNumberNumeric')?.value || '';
      const lettersValue =
        this.PlateInputForm.get('plateNumberLetters')?.value || '';

      this.parentForm.controls[this.controlName].setValue(
        numberValue + ' ' + lettersValue
      );

      this.updateParentForm();
    });
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }

  updateParentForm(event: Event | any = null) {
    this.valueChange.emit(this.getCombinedPlateNumber());
    const numberValue =
      this.PlateInputForm.get('plateNumberNumeric')?.value || '';
    const lettersValue =
      this.PlateInputForm.get('plateNumberLetters')?.value || '';
    const combinedValue = `${numberValue} ${lettersValue}`;

    this.parentForm.controls[this.controlName].setValue(combinedValue);
    this.parentForm.controls[this.controlName].updateValueAndValidity();
    this.parentForm.controls[this.controlName].markAllAsTouched();
    this.valueChange.emit(combinedValue);
  }

  getCombinedPlateNumber(): string {
    const numeric = this.PlateInputForm.get('plateNumberNumeric')?.value || '';
    const letters = this.PlateInputForm.get('plateNumberLetters')?.value || '';
    return `${numeric} ${letters}`.trim();
  }

  patchValuesToForm(plateNumber: string) {
    const numericPart = plateNumber.slice(0, this.maxNumberLength);
    const lettersPart = plateNumber.slice(this.maxNumberLength);

    this.PlateInputForm.patchValue({
      plateNumberNumeric: numericPart,
      plateNumberLetters: lettersPart,
    });

    this.PlateInputForm.updateValueAndValidity();
  }

  resetForm() {
    this.PlateInputForm.reset();
  }
}
