import { NgClass } from '@angular/common';
import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FormsModule } from '@angular/forms';
import {
  debounceTime,
  distinctUntilChanged,
  Subject,
  Subscription,
} from 'rxjs';

@Component({
  selector: 'custom-search-input',
  templateUrl: './custom-search-input.component.html',
  styleUrl: './custom-search-input.component.css',
  imports: [FormsModule],
})
export class CustomSearchInputComponent {
  @Input() class: string = '';
  @Input() labelClass: string = '';
  @Input() label: string = '';
  @Input() placeholder: string = '';
  @Input() name: string = '';
  @Input({ required: true }) value: any;
  @Input() style: 'base' | 'white' | 'grey' = 'base'; // ps. these have effect on style and color
  // @Output() valueChange = new EventEmitter<any>();
  // @Input() model: string = '';
  @Output() modelChange = new EventEmitter<string>();

  @Output() search = new EventEmitter<string>();
  @Output() valueChange = new EventEmitter<string>();
  private inputSubject = new Subject<string>();
  constructor() {
    this.inputSubject
      .pipe(debounceTime(1000), distinctUntilChanged(), takeUntilDestroyed())
      .subscribe((val) => this.search.emit(val.trim()));
  }

  onInputChange(value: string) {
    this.modelChange.emit(value); // for ngModel sync
    this.inputSubject.next(value); // for debounce emit
  }
}
